import React, { Profiler } from 'react';

import AbsoluteLayout from '@splunk/dashboard-layouts/AbsoluteLayout';
import GridLayout from '@splunk/dashboard-layouts/GridLayout';

import datasourcePresets from './DatasourcePresets';
import eventHandlerPresets from './EventHandlerPresets';
import inputPresets from './InputPresets';
import vizPresets from './VisualizationPresets';

export const onRender = (
    id, // the "id" prop of the Profiler tree that has just committed
    phase, // either "mount" (if the tree just mounted) or "update" (if it re-rendered)
    actualDuration // time spent rendering the committed update
    // baseDuration, // estimated time to render the entire subtree without memoization
    // startTime, // when React began rendering this update
    // commitTime, // when React committed this update
    // interactions // the Set of interactions belonging to this update
) => {
    // eslint-disable-next-line no-console
    console.info(id, phase, actualDuration);
};

const withProfiler = (LayoutComponent) => (props) => (
    <Profiler
        id={LayoutComponent.displayName || 'ProfiledLayout'}
        onRender={onRender}
    >
        <LayoutComponent {...props} />
    </Profiler>
);

export default {
    layouts: {
        absolute: withProfiler(AbsoluteLayout),
        grid: withProfiler(GridLayout),
    },
    ...datasourcePresets,
    ...eventHandlerPresets,
    ...inputPresets,
    ...vizPresets,
};
